<template>
  <div class="feedback">
    <div class="text-container">
      <div>提交成功</div>
      <div>感谢您的参与~</div>
    </div>
    <!-- <div class="bottom">
      <img class="qr" src="../assets/qr.png" />
    </div> -->
    <button class="show" v-on:click="show">作品展示</button>
  </div>
</template>
<script>
  export default {
    methods: {
      show () {
        this.$router.push({
          path: 'show'
        })
      }
    }
}
</script>
<style scoped>
.feedback {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.text-container {
  width: 90vw;
  height: 50vh;

  margin: 0 auto;
  margin-top: 15vh;

  display: flex;
  flex-direction: column;
  justify-content: center;

  background: url(../assets/feedback_bg.svg) no-repeat center;

  font-size: 5vw;
  line-height: 8vw;
}
.index {
  color: #84AD4F;
  font-size: 6vw;
}
.bottom {
  width: 90vw;
  height: 50vw;
  background: url(../assets/feedback_bg2.svg) no-repeat center;

}
.qr {
  width: 18vw;
  height: 18vw;

  margin-left: 65vw;
  margin-top: 25vw;
}
.show {
  margin-top: 10px;
  width: 150px;
  height: 60px;
  background: #EECA7D;
  border-radius: 5vw;
  border-color: #EECA7D;
  color: white;
  font-size: 20px;
}
</style>



