<template>
  <div class="show">
    <button class="back" v-on:click="back">返回</button>
    <div class="item" 
      v-for="(item, idx) in list"
      :key="idx">
      <div class="index"
        v-bind:style="{ background: border }">{{idx+1}}
      </div>
      <img class="pic"
        v-bind:src="item.attributes.url.attributes.url"
        v-bind:style="{ borderColor: border }">
      </img>
    </div>
  </div>
</template>

<script>
// import config from '../config'
import AV from 'leancloud-storage'

export default {
  data () {
    return {
      list: [],
      border: '#85AE51'
    }
  },
  mounted () {
    document.title = '作品展示'
    new AV.Query('canvas').find().then((object) => {
      this.list = this.getRandomArrayElements(object, 10)
    }, (error) => {
      console.error(error)
      alert(error)
    })
  },
  methods: {
    getRandomArrayElements: function (arr, count) {
      let shuffled = arr.slice(0)
      let i = arr.length
      let min = i - count
      let temp
      let index
      while (i-- > min) {
        index = Math.floor((i + 1) * Math.random())
        temp = shuffled[index]
        shuffled[index] = shuffled[i]
        shuffled[i] = temp
      }
      return shuffled.slice(min)
    },
    back () {
      this.$router.push({
        path: 'main'
      })
    }
  }
}
</script>

<style scoped>
.main {
  /*margin: 5vw;*/
}

.item {
  position: relative;
  margin: 2vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.index {
  /*background: #ff5800;*/
  color: #ffffff;
  width: 5vw;
  height: 5vw;
  line-height: 5vw;
  border-radius: 50%;
  font-size: 3vw;
  margin: 2vw;
  /*vertical-align: middle;*/
}
.pic {
  height: auto;
  width: 95vw;
  object-fit: cover;

  border: 1.5px solid;
  border-radius: 5vw;
  /*border-color: #ff5800;*/
}
.back {
  margin-top: 10px;
  width: auto;
  height: auto;
  background: #85AE51;
  border-radius: 5vw;
  border-color: #85AE51;
  color: white;
}
</style>



